.blob-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
  pointer-events: none;
  contain: layout style paint;
}
.blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(60px);
  opacity: 0.20;
  transition: opacity 0.3s ease;
}
.blob-1 {
  width: 600px;
  height: 600px;
  background-color: #FF6B95;
  top: -200px;
  right: -200px;
}
.blob-2 {
  width: 700px;
  height: 700px;
  background-color: #7B68EE;
  bottom: -300px;
  left: -300px;
}
.blob-3 {
  width: 400px;
  height: 400px;
  background-color: #61DBFB;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.dark .blob-1 {
  background-color: #FF8FA3;
}
.dark .blob-2 {
  background-color: #9B8AFF;
}
.dark .blob-3 {
  background-color: #7DE5FF;
}
.surface-card {
  background: rgba(255, 255, 255, var(--surface-opacity-heavy));
  backdrop-filter: blur(var(--blur-sm));
  -webkit-backdrop-filter: blur(var(--blur-sm));
  border: 1px solid var(--border-inner);
  box-shadow: 0 0 0 1px var(--border-outer),
    var(--shadow-md);
  border-radius: 8px;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
.dark .surface-card {
  background: rgba(30, 30, 30, var(--surface-opacity-heavy));
}
.surface-border {
  position: relative;
  border: 1px solid var(--border-inner);
  box-shadow: 0 0 0 1px var(--border-outer),
    var(--shadow-md);
  border-radius: 8px;
}
.dark .surface-border {
  box-shadow: 0 0 0 1px var(--border-outer),
    var(--shadow-md);
}
.top-navigation {
  position: sticky;
  top: 0;
  background: var(--bg-overlay);
  backdrop-filter: blur(var(--blur-lg)) saturate(180%);
  -webkit-backdrop-filter: blur(var(--blur-lg)) saturate(180%);
  border-bottom: 1px solid var(--border-primary);
  box-shadow: var(--shadow-sm);
  z-index: var(--z-sticky);
}
.card {
  background: var(--bg-elevated);
  border: 1px solid var(--border-inner);
  box-shadow: 0 0 0 1px var(--border-outer),
    var(--shadow-md);
  border-radius: 8px;
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 0 1px var(--border-outer),
    0 0 0 2px var(--border-inner),
    var(--shadow-lg);
}
.card-container {
  contain: layout style;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideInFromLeft {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes shimmer {
  0% {
    background-position: -200px 0;
  }
  100% {
    background-position: calc(200px + 100%) 0;
  }
}
.animate-fade-in-up {
  animation: fadeInUp 0.4s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}
.animate-slide-in-left {
  animation: slideInFromLeft 0.3s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}
*:focus-visible {
  outline: 2px solid var(--apple-blue);
  outline-offset: 2px;
  border-radius: 4px;
}
.skeleton {
  background: linear-gradient(90deg,
    var(--border-secondary) 0px,
    var(--bg-elevated) 40px,
    var(--border-secondary) 80px
  );
  background-size: 200px 100%;
  animation: shimmer 1.5s infinite ease-in-out;
}
.card-hover {
  transition: box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
.card-hover:hover {
  box-shadow: var(--shadow-lg);
}
.dark .author-button {
  color: #93c5fd !important;
}
table {
  width: 100%;
  border-collapse: collapse;
  background: var(--bg-elevated);
  border: 1px solid var(--border-primary);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: var(--shadow-sm);
}
th, td {
  padding: 12px 16px;
  text-align: left;
  border-bottom: 1px solid var(--border-secondary);
  color: var(--text-primary);
}
th {
  background: var(--bg-surface);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  border-bottom: 2px solid var(--border-primary);
}
tr:hover {
  background: var(--bg-surface);
}
tr:last-child td {
  border-bottom: none;
}
.dark table {
  background: var(--bg-elevated);
  border-color: var(--border-primary);
}
.dark th {
  background: var(--bg-surface);
  color: var(--text-primary);
  border-bottom-color: var(--border-primary);
}
.dark td {
  color: var(--text-primary);
  border-bottom-color: var(--border-secondary);
}
